<script>
import axios from "axios";

import Exam from "../units-exams/exam-component.vue";
import Solution from "../units-exams/result.vue";
import { url, eventBus } from "../../main";
import { generateExamModel } from "../../functions";

export default {
  async mounted() {
    // document.oncontextmenu = () => this.$router.push("/");
    // document.onmouseleave = () => this.$router.push("/");
    this.open = true;
  },
  async created() {
    this.unit = +this.$route.params.unit;
    const stage = this.$route.params.stage;
    this.loading1 = true;
    ///////////////////////// visitor content ///////////////////////////
    if (stage) {
      axios
        .get("/fetchLessonsAndExamsForVisitors/" + stage + "/" + this.unit)
        .then((res) => {
          this.loading1 = false;
          this.lessons = res.data.videos;
          this.exams = res.data.exams;
        });
      return;
    }
    ////////////////////// signed user confirmation system ////////////////////
    this.getLessonsAndExamsForStudent();
    // this.getExams();
  },
  data() {
    return {
      lessons: [],
      exams: [],
      number: null,
      exam_number: null,
      open: true,
      loading1: false,
      loading2: false,
      videoStarted: false,
      unit: null,
      //////////////////
      lesson: null,
      exam: null,
      solutionModel: null,
      solution: null,
      examLoading: false,
      routerKey: new Date().getTime(),
    };
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
    userId() {
      return this.$store.getters.userId;
    },
    username() {
      return this.$store.getters.username;
    },
    userData() {
      return { username: this.userEmail, phone: this.$store.getters.userPhone };
    },
    groupedLessons() {
      const lessons = this.lessons.reduce(
        (groups, item) => ({
          ...groups,
          [item.name]: [...(groups[item.name] || []), item],
        }),
        {}
      );
      return lessons;
    },
  },
  watch: {
    $route(to, from) {
      this.routerKey = new Date().getTime().toString();
    },
    open(val) {
      this.$store.dispatch("toggleHeader", !val);
    },
  },
  methods: {
    async startLesson(id) {
      if (!id) {
        if (this.userId)
          return alert("          ");
        else return alert("   ");
      }
      this.open = false;
      this.$router.push({
        path: "/videos/" + this.$route.params.unit + "/lesson/" + id,
      });
    },
    startExam(id, i) {
      if (!id) {
        if (this.userId)
          return alert("          ");
        else return alert("   ");
      }

      this.open = false;
      this.$router.push(`/videos/${this.$route.params.unit}/exam-controller/${id}`);
    },
    toggle() {
      this.open = !this.open;
    },
    getLessonsAndExamsForStudent() {
      axios
        .get(
          "/fetchLessonsAndExamsForStudent/" +
            this.$store.getters.userId +
            "/" +
            this.$store.getters.stage +
            "/" +
            this.unit
        )
        .then((res) => {
          this.loading1 = false;
          if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);

          this.lessons = res.data.videos;
          this.exams = res.data.exams;
        });
    },
    // getExams() {
    //   const unit = this.$route.params.unit;
    //   const stage = this.$store.getters.stage || this.$route.params.stage;
    //   this.loading2 = true;
    //   axios.get(`/fetchUnitExamsForUser/${stage}/${unit}`).then((res) => {
    //     this.loading2 = false;
    //     this.exams = res.data.exams;
    //   });
    // },
  },
};
</script>

